.header__padding {
    padding: 5rem 6rem;
  }
  

.app__header {
    background-color: var(--color-black);
}

.app__header-h1 {
    font-family: var(--font-main);
    font-weight: 300;
    color: var(--color-main);
    letter-spacing: 0.04em;
    line-height: 117px;
    font-size: 90px;
}

.app__wrapper-img img {
    width: 100%;
}

@media screen and (max-width: 1150px) {
    .header__padding {
        padding-left: 6rem;
    }
    .app__wrapper-img img {
        width: 100%;
    }
}

@media screen and (max-width: 650px) {
    .app__header-h1 {
        font-size: 70px;
        line-height: 87px;
    }
}

@media screen and (max-width: 450px) {
    .app__header-h1 {
        font-size: 50px;
        line-height: 70px;
    }
    .header__padding {
        padding: 6rem 3rem 3rem 3rem;
      }
}